* {
  margin: 0;
  padding: 0;
}
html {
  font-size: 16pt;
}
body {
  background-color: #f8f8fb;
  font-family: "Lab Grotesque", serif;
}
@media (min-width: 480px) {
  .appWrapper {
    margin: 0 auto;
    position: relative;
  }
}
.shoppingList {
  width: 100%;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  background-color: #e5e5e5;
}
.shoppingListHeader {
  min-height: 8vh;
  background-color: #2291ff;
  display: flex;
  align-items: center;
  justify-content: center;
}
@media (min-width: 480px) {
  .shoppingListHeader {
    position: sticky;
    top: 0;
  }
}
.shoppingListHeader h2 {
  font-size: 18pt;
  text-align: center;
  color: white;
  font-weight: 500;
}
.itemContentLeftPart {
  display: flex;
  border-radius: 10px;
  border: 1px solid #d9d9d9;
  background-color: #ffffff;
  margin: 20px 0;
  padding: 0.5rem;
  font-size: 14pt;
  user-select: none;
  box-sizing: border-box;
  word-break: break-word;
}
.shoppingListHeader {
  background-color: #E5E5E5;
  border-bottom: 1px solid rgba(0, 0, 0, 0.12);
  position: relative;
}
.shoppingListHeader h2 {
  color: black;
}
.shoppingListHeader svg {
  position: absolute;
  left: 1rem;
}
.itemLabel {
  display: flex;
  flex-direction: column;
}
.itemInfo > span {
  color: #999999;
  font-size: 12pt;
}
.shoppingListItems {
  padding: 1rem;
  box-sizing: border-box;
  max-width: 720px;
  width: 100%;
  margin: 0 auto;
}
.boughtProduct {
  border: 1px solid #999999;
  background-color: #cccccc;
}
.shoppingListItemOptions {
  color: #616161;
  transition: font-size 0.5s, margin-top 0.5s, color 0.5s;
  width: 100%;
}
.openedOptions {
  margin-top: 25px;
  font-size: 0.875rem;
  color: #616161;
}
.closedOptions {
  margin-top: 0;
  font-size: 0;
  color: rgba(255, 255, 255, 0);
  overflow: hidden;
}
.arrowReverse {
  transform: scaleY(-1);
}
.arrow {
  transition: transform 0.5s ease-out;
  padding-left: 8px;
}
.icon {
  height: 1.125rem;
  width: 20px;
}
.icon > path {
  fill: black;
}
