* {
  margin: 0;
  padding: 0;
}
html {
  font-size: 16pt;
}
body {
  background-color: #f8f8fb;
  font-family: "Lab Grotesque", serif;
}
@media (min-width: 480px) {
  .appWrapper {
    margin: 0 auto;
    position: relative;
  }
}
.shoppingList {
  width: 100%;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  background-color: #e5e5e5;
}
.shoppingListHeader {
  min-height: 8vh;
  background-color: #2291ff;
  display: flex;
  align-items: center;
  justify-content: center;
}
@media (min-width: 480px) {
  .shoppingListHeader {
    position: sticky;
    top: 0;
  }
}
.shoppingListHeader h2 {
  font-size: 18pt;
  text-align: center;
  color: white;
  font-weight: 500;
}
.itemWrapper {
  display: flex;
  justify-content: space-between;
  margin: 10px 0;
  border-radius: 10px;
  width: 100%;
  border: 1px solid #ebebeb;
  background-color: #ffffff;
  font-family: "Lab Grotesque", serif;
}
.itemWrapper:hover {
  border: 1px solid rgba(0, 0, 0, 0.16);
}
.itemContentWrapper {
  display: flex;
  justify-content: space-between;
  width: 100%;
}
.itemContentLeftPart {
  display: flex;
  justify-content: start;
  align-items: start;
  padding: 20px 20px;
  font-size: 18pt;
  height: 100%;
  user-select: none;
  box-sizing: border-box;
  width: 100%;
}
.itemContentLeftPart label {
  font-size: 1.125rem;
}
.inputWrapper {
  margin: 20px 0;
  border: 1px solid #ebebeb;
  border-radius: 10px;
  padding: 20px 20px;
  box-sizing: border-box;
  font-size: 1.125rem;
  background-color: #ffffff;
}
.emptyList {
  text-align: center;
  margin-bottom: 20px;
  height: 50vh;
  display: flex;
  justify-content: center;
  align-items: flex-end;
}
.emptyList p {
  text-align: center;
}
.emptyListWrapper {
  background-color: #e5e5e5;
  padding: 1rem;
  flex-grow: 1;
  display: flex;
  box-sizing: border-box;
  max-width: 720px;
  width: 100%;
  margin: 0 auto;
}
.emptyListContent {
  background-color: white;
  border-radius: 1rem;
  width: 100%;
}
.buttonContainer {
  margin: 0 auto;
  width: 9rem;
}
.content {
  display: flex;
  flex-direction: column;
  height: 100%;
  flex-grow: 1;
}
.buttonSticky {
  position: sticky;
  bottom: 0px;
  padding-bottom: 1rem;
  background-color: #e5e5e5;
}
.header {
  padding: 1rem 1rem 0;
  box-sizing: border-box;
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  max-width: 720px;
  width: 100%;
  margin: 0 auto;
}
.kebab {
  display: flex;
  align-items: center;
  height: 100%;
}
.actions {
  position: absolute;
  background-color: #FFFFFF;
  border-radius: 4px;
  display: flex;
  flex-direction: column;
  right: -10px;
  top: 60px;
  box-shadow: 0px 0px 3px rgba(0, 0, 0, 0.25);
}
.kebabIcons {
  display: flex;
  gap: 10px;
  padding: 1rem 2rem;
  border-bottom: 1px solid rgba(0, 0, 0, 0.12);
  z-index: 3;
  background-color: white;
  font-size: 14pt;
}
.triangle {
  background-color: white;
  width: 20px;
  height: 20px;
  position: absolute;
  transform: rotate(45deg);
  top: -7px;
  right: 35px;
  box-shadow: 0px 0px 3px rgba(0, 0, 0, 0.25);
  z-index: 1;
}
.allProducts {
  padding: 20px;
  font-size: 1.125rem;
  box-sizing: border-box;
  width: 100%;
  background-color: white;
  border-radius: 8px;
  margin-bottom: 10px;
}
.clickZone {
  padding-left: 1rem;
}
