@import '../../../Variables.less';

.sortShell{
    display: flex;
    flex-direction: column;
    gap: 5px;
    background-color: white;
    box-shadow:  0 0 10px 4px rgba(99, 99, 99, 0.1);
    font-size: 14pt;
    position: absolute;
    right: 1rem;
    top: 2rem;
    z-index: 2;
}
.sortShell > div {
    padding: 0.5rem 1rem;
    white-space: nowrap;
}

.sortShell > h3{
    padding: 0.5rem 1rem;
}

.sortShell input[type="radio"]{
    visibility: hidden;
    height: 0;
    width: 0;
}

.checked{
    background-color:  @color-blue;;
    color: white;
  }